import styled from "styled-components";

export const ImagesWrapper = styled.div`
  width: 150px;
  height: 150px;
  display: flex;
  justify-content: center;
  position: relative;

  & img {
    width: 100%;
    height: 100%;
    position: absolute;
    object-fit: cover;
    top: 0;
    left: 0;
    z-index: 1;
  }
`;

export const AddPhotoButton = styled.input`
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 2;

  &:hover {
    cursor: pointer;
  }

  &::-webkit-file-upload-button {
    visibility: hidden;
  }

  &::before {
    display: block;
    outline: none;
    content: '';
    white-space: nowrap;
    width: 100%;
    height: 100%;
    border-radius: 5px;
    border: 2px dashed ${({ theme }) => theme.colors.mainColor};
  }

  &::after {
    width: 40px;
    height: 40px;
    border: 1px solid ${({ theme }) => theme.colors.mainColor};
    border-radius: 100%;
    content: '+';
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 40px;
    color: ${({ theme }) => theme.colors.mainColor};
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: transform 0.4s ease-in-out;
    transform-origin: center;
  }

  &:hover::after {
      transform: translate(-50%, -50%) scale(1.3) rotate(180deg);
    }
`;