import React, { useEffect, useCallback } from 'react';
import PropTypes from 'prop-types';
import { useForm } from 'react-hook-form';
import { useDropzone } from 'react-dropzone';
import { ImagesWrapper, AddPhotoButton } from './FileInput.style';

const FileInput = ({ name, type, accept, mode = 'append' }) => {
  const { register, unregister, setValue, watch } = useForm();
  const files = watch(name);

  const onDrop = useCallback(
    (droppedFiles) => {
      let newFiles =
        mode === 'update' ? droppedFiles : [...(files || []), ...droppedFiles];
      if (mode === 'append') {
        newFiles = newFiles.reduce((prev, file) => {
          const fo = Object.entries(file);
          if (
            prev.find((e) => {
              const eo = Object.entries(e);
              return eo.every(
                ([key, value], index) =>
                  key === fo[index][0] && value === fo[index][1]
              );
            })
          ) {
            return prev;
          } else {
            return [...prev, file];
          }
        }, []);
      }
      setValue(name, newFiles, { shouldValidate: true });
    },
    [setValue, name, mode, files]
  );

  const { getRootProps, getInputProps, isDragActive } = useDropzone({
    onDrop,
    accept: accept,
  });

  const renderPreview = files?.map((file) => (
    <img key={file.name} src={URL.createObjectURL(file)} />
  ));

  useEffect(() => {
    register(name);
    return () => {
      unregister(name);
    };
  }, [register, unregister, name]);

  return (
    <ImagesWrapper {...getRootProps()}>
      <AddPhotoButton style={{display: 'block'}} type={type} {...getInputProps()} />
      {!!files?.length && renderPreview}
    </ImagesWrapper>
  );
};

FileInput.propTypes = {};

export default FileInput;
